// =============================================================================
// Endpoint Validation Utilities
// =============================================================================
// Validation and limits for API endpoints

export interface ValidationResult {
  valid: boolean;
  errors: string[];
}

const MAX_LOOKBACK_DAYS = 90;
const MAX_PAGINATION_LIMIT = 1000;
const MAX_WEBHOOKS_PER_API_KEY = 50;
const MAX_WEBHOOK_URL_LENGTH = 2048;

/**
 * Validate date range for analytics/billing queries
 */
export function validateDateRange(
  startDate: string | null,
  endDate: string | null
): ValidationResult {
  const errors: string[] = [];

  if (!startDate || !endDate) {
    return { valid: true, errors: [] }; // Optional dates
  }

  const start = new Date(startDate);
  const end = new Date(endDate);

  if (isNaN(start.getTime())) {
    errors.push('Invalid startDate format. Use ISO 8601 format.');
  }

  if (isNaN(end.getTime())) {
    errors.push('Invalid endDate format. Use ISO 8601 format.');
  }

  if (errors.length > 0) {
    return { valid: false, errors };
  }

  if (start > end) {
    errors.push('startDate must be before endDate');
  }

  const daysDiff = (end.getTime() - start.getTime()) / (1000 * 60 * 60 * 24);
  if (daysDiff > MAX_LOOKBACK_DAYS) {
    errors.push(`Date range cannot exceed ${MAX_LOOKBACK_DAYS} days`);
  }

  // Prevent queries too far in the past
  const maxPastDate = new Date();
  maxPastDate.setDate(maxPastDate.getDate() - MAX_LOOKBACK_DAYS);
  if (start < maxPastDate) {
    errors.push(`startDate cannot be more than ${MAX_LOOKBACK_DAYS} days in the past`);
  }

  // Prevent future dates
  const now = new Date();
  if (end > now) {
    errors.push('endDate cannot be in the future');
  }

  return {
    valid: errors.length === 0,
    errors,
  };
}

/**
 * Validate pagination parameters
 */
export function validatePagination(
  limit: number | null,
  offset: number | null
): ValidationResult {
  const errors: string[] = [];

  if (limit !== null) {
    if (limit < 1) {
      errors.push('limit must be at least 1');
    }
    if (limit > MAX_PAGINATION_LIMIT) {
      errors.push(`limit cannot exceed ${MAX_PAGINATION_LIMIT}`);
    }
  }

  if (offset !== null) {
    if (offset < 0) {
      errors.push('offset must be non-negative');
    }
  }

  return {
    valid: errors.length === 0,
    errors,
  };
}

/**
 * Validate webhook URL
 */
export function validateWebhookUrl(url: string): ValidationResult {
  const errors: string[] = [];

  if (!url || typeof url !== 'string') {
    errors.push('URL is required and must be a string');
    return { valid: false, errors };
  }

  if (url.length > MAX_WEBHOOK_URL_LENGTH) {
    errors.push(`URL cannot exceed ${MAX_WEBHOOK_URL_LENGTH} characters`);
  }

  try {
    const parsed = new URL(url);
    
    // Only allow HTTPS in production (can be configured)
    if (process.env.NODE_ENV === 'production' && parsed.protocol !== 'https:') {
      errors.push('Webhook URLs must use HTTPS in production');
    }

    // Disallow localhost/internal IPs in production
    if (process.env.NODE_ENV === 'production') {
      const hostname = parsed.hostname.toLowerCase();
      if (
        hostname === 'localhost' ||
        hostname === '127.0.0.1' ||
        hostname.startsWith('192.168.') ||
        hostname.startsWith('10.') ||
        hostname.startsWith('172.16.') ||
        hostname.startsWith('172.17.') ||
        hostname.startsWith('172.18.') ||
        hostname.startsWith('172.19.') ||
        hostname.startsWith('172.20.') ||
        hostname.startsWith('172.21.') ||
        hostname.startsWith('172.22.') ||
        hostname.startsWith('172.23.') ||
        hostname.startsWith('172.24.') ||
        hostname.startsWith('172.25.') ||
        hostname.startsWith('172.26.') ||
        hostname.startsWith('172.27.') ||
        hostname.startsWith('172.28.') ||
        hostname.startsWith('172.29.') ||
        hostname.startsWith('172.30.') ||
        hostname.startsWith('172.31.')
      ) {
        errors.push('Webhook URLs cannot point to localhost or private IP addresses in production');
      }
    }
  } catch (e) {
    errors.push('Invalid URL format');
  }

  return {
    valid: errors.length === 0,
    errors,
  };
}

/**
 * Validate webhook events array
 */
export function validateWebhookEvents(events: any): ValidationResult {
  const errors: string[] = [];

  if (!Array.isArray(events)) {
    errors.push('events must be an array');
    return { valid: false, errors };
  }

  if (events.length === 0) {
    errors.push('At least one event type is required');
  }

  if (events.length > 20) {
    errors.push('Cannot subscribe to more than 20 event types per webhook');
  }

  const validEvents = [
    'payment.verified',
    'payment.failed',
    'usage.recorded',
    'session.completed',
    'endpoint.created',
    'endpoint.updated',
    'endpoint.deleted',
  ];

  for (const event of events) {
    if (typeof event !== 'string') {
      errors.push('All events must be strings');
      break;
    }
    if (!validEvents.includes(event)) {
      errors.push(`Invalid event type: ${event}. Valid types: ${validEvents.join(', ')}`);
    }
  }

  return {
    valid: errors.length === 0,
    errors,
  };
}

/**
 * Check if API key has reached webhook limit
 */
export function checkWebhookLimit(currentCount: number): ValidationResult {
  if (currentCount >= MAX_WEBHOOKS_PER_API_KEY) {
    return {
      valid: false,
      errors: [`Maximum of ${MAX_WEBHOOKS_PER_API_KEY} webhooks per API key`],
    };
  }
  return { valid: true, errors: [] };
}

/**
 * Validate granularity parameter
 */
export function validateGranularity(granularity: string | null): ValidationResult {
  const errors: string[] = [];

  if (granularity && !['hour', 'day', 'week'].includes(granularity)) {
    errors.push('granularity must be one of: hour, day, week');
  }

  return {
    valid: errors.length === 0,
    errors,
  };
}

/**
 * Validate period type
 */
export function validatePeriodType(periodType: string | null): ValidationResult {
  const errors: string[] = [];

  if (periodType && !['daily', 'weekly', 'monthly', 'custom'].includes(periodType)) {
    errors.push('periodType must be one of: daily, weekly, monthly, custom');
  }

  return {
    valid: errors.length === 0,
    errors,
  };
}






